// Map every variable into a css variable
:root {
    @each $map_name, $map in $variables {
        @each $key, $value in $map {
            --#{$map_name}-#{$key}: $value;
        }
    }
}


html, body {
    font-size: 10px;
    color: text-color('base');
}


