.a-button {
    font-family: Roboto, sans-serif;
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;

    font-size: 1.4rem;
    font-weight: 500;
    letter-spacing: 0.04em;
    line-height: 3.6rem;

    text-decoration: none;
    text-transform: uppercase;

    display: inline-block;
    position: relative;

    box-sizing: border-box;

    min-width: 8.8rem;
    height: 3.6rem;
    padding: 0 1.6rem;

    border: none;
    outline: none;
    text-align: center;

    user-select: none;

    -webkit-appearance: none;
    -webkit-tap-highlight-color: transparent;

    overflow: hidden;
    vertical-align: middle;

    border-radius: 2px;

    transition: all .2s ease-out;

    & + & {
        margin-left: .375em;
    }

    &:active {
        outline: none;

        transform: translate(2px, 2px);
        box-shadow: inset .1em .1em .1em rgba(black, .4);
    }

    &:hover {
        cursor: pointer;
    }

    &::-moz-focus-inner {
        padding: 0;
        border: 0;
    }

    &:disabled {
        background-color: transparent;
        color: rgba(0, 0, 0, 0.38);
        cursor: default;
        pointer-events: none;
        box-shadow: none;
    }

    &:not(:disabled) {
        //background-color: transparent;
        //color: color(primary);
        //color: inherit;

        will-change: transform, opacity;
        -webkit-tap-highlight-color: transparent;
    }

    &--active {
        &:after {
            display: block;
            content: '';

            position: absolute;
            left: 0;
            right: 0;
            bottom: 0;

            border-top: 2px solid currentColor;
        }
    }

    /**********************************************
     * Custom button styles                       *
     **********************************************/
    &--primary {
        background-color: color('primary');
        color: text-color('primary');

        box-shadow: .1em .1em .1em rgba(black, .4);

        &:hover, &:focus {

        }
        &:active {
            box-shadow: 0 0 0 rgba(black, .4);
        }
    }
}


